<template>
  <div id="selectDeptAndPositions">
    <v-text-field
    label="*部门/岗位"
    required 
    :rules="nameRules"
    @click="selectDeptAndPositionsDialog = true"
    v-model="displayDeptAndPositions"
    readonly
    ></v-text-field>
    <v-dialog v-model="selectDeptAndPositionsDialog" fullscreen hide-overlay transition="dialog-bottom-transition">
      <v-card>
        <v-card-title>
          <span class="headline">部门/岗位</span>
        </v-card-title>

        <v-card-text>
          <v-container>
            <v-row>
              <v-divider vertical></v-divider>
              <v-col cols="12" sm="6" md="6">
                <v-treeview
                  :active.sync="active"
                  :items="departments"
                  :open.sync="open"
                  color="warning"
                  return-object
                  hoverable
                  transition
                  :selectable="selectable"
                  selection-type="independent"
                  v-model="selections"
                >
                  <template v-slot:prepend="{ open }">
                    <v-icon
                      :color="open ? 'green' : 'purple'"
                    >{{open ? 'mdi-file-tree-outline' : 'mdi-file-tree'}}</v-icon>
                  </template>
                </v-treeview>
              </v-col>

              <v-divider vertical></v-divider>

              <v-col class="d-flex text-center">
                <v-scroll-y-transition mode="out-in">
                  <v-row>
                    <template v-for="(selection, i) in selections">
                      <v-col cols="6" :key="'a'+i">
                        <v-text-field label="部门" :value="selection.name"></v-text-field>
                      </v-col>
                      <v-col cols="5" :key="'b'+i">
                        <v-select
                          multiple
                          v-model="tempDeptIdAndPositions[selection.id]"
                          :items="positionList"
                          label="岗位"
                        ></v-select>
                      </v-col>
                      <v-col cols="1" :key="'c'+i">
                        <v-icon title="删除" style="margin-top: 22px;" @click.stop="removeGroupAndPosition(i)" >mdi-delete</v-icon>
                      </v-col>
                    </template>
                  </v-row>
                </v-scroll-y-transition>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="close">取消</v-btn>
          <v-btn color="blue darken-1" text @click="save">保存</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  data: () => ({
    active: [],
    avatar: null,
    open: [],
    selectable: true,
    selectDeptAndPositionsDialog: false,
    positionList: [],
    selections: [],
    tempDeptIdAndPositions: {},
    displayDeptAndPositions: "",

    //表单验证
    nameRules: [v => !!v || "该选项为必填字段"],
  }),

  props: {
    departments: {
      type: Array
    },
    selectDeptAndPositionNodes: {
      type: Array
    },
  },

  mounted() {
    this.init();
    
  },

  computed: {
      
  },

  watch: {
    selectDeptAndPositionsDialog(val) {
      val || this.close();
    },
    selectDeptAndPositionNodes() {
      this.init();
    }
  },

  created() {
    this.initPositions();
  },

  methods: {
    initPositions() {
      let params = {
        type: "position"
      };
      this.$http
        .get("/user/api/v1/dictionary/listValues", params)
        .then(res => {
          if (res.success) {
            this.positionList = res.data;
          }
        })
        .catch(error => {
          this.$store.commit("SET_SNACK", {
            success: false,
            msg: "访问异常：获取岗位失败"
          });
        });
    },

    init() {
      let _this = this;
      let displayDeptAndPositionArray = [];
      this.selections = [];
      this.open = [];
      this.tempDeptIdAndPositions = {};

      if(!this.selectDeptAndPositionNodes) {
        return;
      }
      
      this.selectDeptAndPositionNodes.forEach(function(
        selection,
        index
      ) {
        let positions = selection.positions;
        if (!!positions && positions.length > 0) {
          displayDeptAndPositionArray.push(
            selection.name + "[岗位:" + positions + "]"
          );
        } else {
          displayDeptAndPositionArray.push(selection.name);
        }
        _this.selections.push(selection);
        
        _this.pushOpenNodes(selection);

        _this.tempDeptIdAndPositions[selection.id] = selection.positions;
      });

      this.displayDeptAndPositions = displayDeptAndPositionArray.join(", ");
    },

    pushOpenNodes(selection) {
      let _this = this;
      let parentIds = selection.parentIds;
      if(!!parentIds) {
        let parentNodes = this.departments;
        parentIds.forEach(function(currentParentNodeId, index) {
          if(!!parentNodes && parentNodes.length > 0){
            parentNodes = _this.pushOpenNodesChildren(parentNodes, currentParentNodeId);
          }
        });
      }
    },

    pushOpenNodesChildren(parentNodes, currentParentNodeId) {
      let _this = this;
      let results = [];

      if(!parentNodes){
        return [];
      }

      parentNodes.forEach(function(selection, index){
          if(selection.id == currentParentNodeId) {
            _this.open.push(selection);
            results = selection.children;
            return;
          }
      });

      return results;
    },

    removeGroupAndPosition(i) {
      this.selections.splice(i,1);
    },

    close() {
      this.selectDeptAndPositionsDialog = false;
    },

    save() {
      this.saveDeptAndPosition();
      this.selectDeptAndPositionsDialog = false;
    },

    saveDeptAndPosition() {
      let _this = this;
      let displayDeptAndPositionArray = [];
      let selectDeptAndPositionNode = {};
      this.selectDeptAndPositionNodes.splice(0);
      this.selections.forEach(function(selection, index) {
        selectDeptAndPositionNode = selection;
        let positions = _this.tempDeptIdAndPositions[selection.id];
        if (!!positions) {
          selectDeptAndPositionNode.positions = positions;
          displayDeptAndPositionArray.push(
            selection.name + "[岗位:" + positions + "]"
          );
        } else {
          selectDeptAndPositionNode.positions = [];
          displayDeptAndPositionArray.push(selection.name);
        }

        _this.selectDeptAndPositionNodes.push(selectDeptAndPositionNode);
      });

      this.displayDeptAndPositions = displayDeptAndPositionArray.join(", ");
    }
  },
};
</script>
